import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
let newRouter = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: resolve => require(['../components/Home'], resolve),
      meta: { title: '首页' }
    },
    {
      path: '/PersonalInformation',
      name: 'PersonalInformation',
      component: resolve => require(['../components/page/PersonalInformation'], resolve),
      meta: { title: '个人信息' }
    },
    {
      path: '/WorkExperience',
      name: 'WorkExperience',
      component: resolve => require(['../components/page/WorkExperience'], resolve),
      meta: { title: '工作经验' }
    },
    {
      path: '/HobbiesInterests',
      name: 'HobbiesInterests',
      component: resolve => require(['../components/page/HobbiesInterests'], resolve),
      meta: { title: '兴趣爱好' }
    },
    {
      path: '/SkillLabel',
      name: 'SkillLabel',
      component: resolve => require(['../components/page/SkillLabel'], resolve),
      meta: { title: '技能标签' }
    },
    {
      path: '/ProjectExperience',
      name: 'ProjectExperience',
      component: resolve => require(['../components/page/ProjectExperience'], resolve),
      meta: { title: '项目经历' }
    },
  ],
  //每次路由跳转后滚轮置顶
  scrollBehavior(to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    return {
      x: 0,
      y: 0
    }
  }
})
newRouter.beforeEach((to, from, next) => {
  next()
})
export default newRouter



