<template>
  <div id="app" @click="play()">
    <audio ref="myAudio" src="static/music/Дастан Оразбеков - Мереке.mp3" loop style="display:none"></audio>
    <div id="music" :class="music?'':'paused'" v-show="autoPlay" @click="musicPlay()">
      <div ref="music" class="music">
        <i class="iconfont iconyinle"></i>
      </div>
    </div>
    <div class="loading" v-if="loading">
      <div class="text_box">
        <div class="text">
          <div class="text_bck"></div>
        </div>
        <svg height="50" width="120">
          <rect id="shape" height="100%" width="100%" />
        </svg>
      </div>
    </div>
    <router-view v-else />
    <svg height="0" width="0">
      <defs>
        <clipPath id="svgTextPath">
          <!-- textLength 文字的长度 此处对应容器的宽度  x y 为横纵坐标位置 -->
          <text x="15" y="32" textLength="90px">Loading</text>
        </clipPath>
      </defs>
    </svg>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      loading: true,
      autoPlay: false,
      music: false
    };
  },
  mounted() {
    setTimeout(() => {
      this.loading = false;
    }, 1500);
  },
  methods: {
    play() {
      if (!this.autoPlay) {
        this.autoPlay = true;
        this.music = true;
        this.$refs.myAudio.play();
      }
    },
    musicPlay() {
      if (!this.music) {
        this.$refs.myAudio.play();
        this.$refs.music.style.animationPlayState = "running";
      } else {
        this.$refs.myAudio.pause();
        this.$refs.music.style.animationPlayState = "paused";
      }
      this.music = !this.music;
    }
  }
};
</script>

<style lang="scss">
#app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
#music {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  right: 1rem;
  top: 1rem;
  position: fixed;
  z-index: 999;
  box-shadow: 0 0 3px #ccc;
  overflow: hidden;
  &:active {
    opacity: 0.7;
  }
  &.paused {
    opacity: 0.5;
  }
  &.paused::before {
    content: "";
    width: 2px;
    height: 3rem;
    position: absolute;
    z-index: 5;
    left: 1.35rem;
    transform: rotate(-45deg);
    background: #6e6e6e;
  }
  .music {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
    background: #f1f1f1;
    animation: musicPlay 3s linear infinite;
    i {
      font-size: 2rem;
      color: var(--base);
    }
  }
}
.loading {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
  .text_box {
    position: relative;
    svg {
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  .text {
    margin: 0 auto;
    background: #ccc;
    width: 120px;
    height: 50px;
    clip-path: url(#svgTextPath);
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    .text_bck {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: var(--base);
      animation: text_bck 0.5s ease infinite alternate;
    }

    #svgTextPath {
      //剪切文字的文字样式 此处的文字颜色取背景色
      text {
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
          Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
        font-weight: 200;
        font-size: 1rem;
      }
    }
  }
}
rect#shape {
  fill: transparent;
  stroke: var(--base);
  stroke-width: 2px;
  /* stroke-dasharray:用于创建虚线，之所以后面跟的是array的，是因为值其实是数组 */
  /* 虚线长 */
  /* 虚线长 间距  */
  /* 虚线长 虚线长 虚线长 */
  animation: dong 0.5s linear infinite;
}

@keyframes text_bck {
  0% {
    transform: scale(0);
    opacity: 0.5;
  }
  100% {
    transform: scale(4);
    opacity: 1;
  }
}

@keyframes dong {
  0% {
    stroke-dasharray: 100 240;
    stroke-dashoffset: 0;
    stroke-opacity: 0.1;
  }

  50% {
    stroke-dasharray: 200 200;
    stroke-dashoffset: -170;
    stroke-opacity: 0.3;
  }

  100% {
    stroke-dasharray: 100 240;
    stroke-dashoffset: -340;
    stroke-opacity: 1;
  }
}
@keyframes musicPlay {
  100% {
    transform: rotate(360deg);
  }
}
</style>



